export const enhanceTags = (tags) => tags.map((tag) => enhanceTag(tag));
const enhanceTag = ({ properties: { id, tagType: type, webTitle: title, twitterHandle, bylineImageUrl, contributorLargeImagePath: bylineLargeImageUrl, }, }) => ({
    id,
    type,
    title,
    twitterHandle,
    bylineImageUrl,
    bylineLargeImageUrl,
});
